@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap"); /** Media Queries **/
body {
  font-family: "Poppins", sans-serif;
  background-color: #2c3e50;
}
body .navbar {
  width: 100%;
  margin: 0;
}

.menu {
  background-color: #2c3e50;
}

.offcanvas {
  background-color: #2c3e50;
}

.btn-orange {
  background-color: #ff9100;
  color: white;
}
.btn-orange:hover {
  background-color: #cc7400;
  color: #e6e6e6;
}

.dropdown-menu {
  background-color: #1f2c38;
}

.dropdown-item:hover {
  background-color: #1a252f;
}

.line-nav {
  color: white;
}

#loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #ff9100;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.formLogin {
  width: 90%;
  margin: auto;
}
@media (min-width: 768px) {
  .formLogin {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .formLogin {
    width: 30rem;
  }
}
.formLogin input {
  border-radius: none;
}

.lista-index .list-group-item {
  border: none;
  transition: 0.2s;
}
.lista-index .list-group-item:hover {
  background-color: #3e5871;
  border: none;
  color: white;
}